         'Definition\r\nAsbestos rope was commonly used in construction as gaskets, or seals that fill the space between two adjacent surfaces. Rope and rope packing are insulation materials that are used to seal and protect boilers, pipes and heaters from fire.\r\n\r\nSynonyms of product\r\nAsbestos Rubber Sheets\r\nLeveraging on our industrious experience, we offer asbestos braided ropes in standard as well as in customer specific options. These are manufactured using white Chrysotile yarn twisted around in m/c. We make use of our finest quality asbestos yarn. These Asbestos rope is also known as Asbestos cord, line, cable, lead, twine, string.\r\n\r\nAsbestos ropes are widely used in the heavy machinery industry in the processes of heat insulation of pipes and boiler sealing, hot blast valves, blue cap doors. They can be effectively used in applications with temperature up to 350 degree Celsius beyond which it starts to crumble. Moreover, these are widely acknowledged for their higher efficiency, low cost maintenance and consistent output.\r\n\r\nProduct wise application search strings\r\nIt was commonly used to make fire retardant blankets, oven mitts, ironing board covers and gaskets for boilers.',
