        productSpecification: 'Minimum Order Quantity        1 Roll',
        productDescription:
         'PTFE (Polytetra-fluoro-ethylene) is a synthetic fluoropolymer accidentally invented by Roy Plunkett that finds numerous applications. It is well known by the DuPont brand name Teflon. PTFE is a material of remarkable properties; non-stick, friction-free, self-lubricating, non-wetting, non-flammable, non-brittle, non-toxic, resistant to atmospheric conditions, resistant to fungus growth and resistant to all chemicals (except molten alkali metals and fluorine at elevated temperatures and pressures). Its electrical properties are equally outstanding. All properties are maintained over a wide working temperature range of -200oC to +350oC.Fiber-Glass too is a material of remarkable properties. Fabrics made from continuous filament yarn exhibit superior properties in terms of high tensile strength, low elongation, high temperature resistance, electric and sound insulation etc.Back in 1980 Unnati, having received technical support from DuPont, was one of the first companies in India to develop a matrix of the two materials - PTFE + Fiberglass i.e. PTFE coated Glass Cloth. Although not the first to develop we are now by far, the largest manufacturer. Various varieties are made on our towers which are the most sophisticated in the country. Should the end application demand colourful fabrics - we have them. Controlled manufacturing processes and rigid inspection procedures give the right quality of the material that is capable of resolving a wide range of problems. Conductive grades of fabrics are also our specialty where the static & charge needs to be handled carefully.Requirements to cover Antennas at high altitudes have been successfully matched with PTFE coated fiber glass fabrics used as Radomes. Special grades have been developed which allows micro-waves to pass through. They protect the Antennas from snow, dust, rain, moisture, bird nest etc. We offer a wider range of PTFE products than any other manufacturer, including PTFE coated Glass Fabric in sheets, rolls, tapes, discs, laminates, belts and threads.',
