         'This Heat Shrink Sleeve 10 mm Black 2 meter Industrial Grade WOER (HST) is excellent for insulation. They have exceptional insulation characteristics, excellent stress control properties; long-term weather and regular wear and tear resistance, ease of installation and reliable performance in even in harshest of the situations.\r\n\r\nThese High performance 2:1 heat shrink material tubes, bundles, and protects cables; wire and hoses are used for a wide range of DIY applications. It is used extensively for cable and wire harnessing, strain relief, insulation, color-coding, identification, and protection.\r\n\r\nSimply a great way to create a Flexible, Heat Resistant and Waterproof tight joint. Just slide a slightly larger tube at the desired location and apply heat through a cigarette lighter, or a heat gun; for about 10 seconds and the tube will shrink down and will hold the joint tightly.\r\n\r\nPackage Includes :\r\n1 x Heat Shrink Sleeve 10mm Black 2meter Industrial Grade WOER (HST) heat shrink tubing provides electrical insulation, mechanical protection, sealing, and strain relief, and it can also be used to identify components and wires, creating a functional, professional appearance of the assembly.\r\n\r\nDiameter:10 mm\r\n\r\nLength: 2 meter(100 meter  roll also available)',
