        productDescription:
         'Electrical Insulating Crepe Paper is one of the most important solid insulation materials used in transformers. Its quality directly influences the life span of the transformer. Even though the base kraft paper is impregnated with liquids to manufacture creped kraft paper, the porosity of the paper remains intact due to the automatic temperature and moisture control by the automated machine. The mechanical, as well as cross mechanical strength, is enhanced as compared to the base kraft paper. The pH value remains intact, a little more towards acidic as preferred in transformer insulation. The BDV is enhanced by up to 80% as compared to base kraft paper. To ensure of the best quality of Crepe paper, the base kraft paper is manufactured using longer fibers of pure wood pulp. This base paper is chemically neutral with low ash, chloride and sulfide content.',
